import { useEffect } from "react"
import { useAppContext } from "../context/appContext"
import Sidebar from "./Sidebar"
import { useState } from "react"
import { Link } from "react-router-dom"

const Followers = () => {
    const { getUserFollowers } = useAppContext()
    const [followers, setFollowers] = useState(undefined)

    let url = window.location.pathname
    url = url.replace("/profile/", "")
    url = url.replace("/followers", "")

    useEffect(() => {
        async function fetchData() {
            const data = await getUserFollowers(url)
            console.log(data)
            setFollowers(data)
        }
        fetchData()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <>
            <Sidebar />
            <div className="follow">
                <div className="follow__container">
                    <div className="follow__top">Followers:</div>

                    <div className="follow__content">
                        {followers &&
                            followers.map((follow) => {
                                return (
                                    <div
                                        className="follow__person"
                                        key={follow._id}
                                    >
                                        <div className="follow__person-data">
                                            <img
                                                src={follow.img}
                                                alt=""
                                                className="follow__person-img"
                                            />
                                            <div className="follow__person-username">
                                                {follow.username}
                                            </div>
                                        </div>
                                        <Link
                                            to={`/profile/${follow.username}`}
                                        >
                                            <button className="follow__person-btn">
                                                Show Profile
                                            </button>
                                        </Link>
                                    </div>
                                )
                            })}
                    </div>
                </div>
            </div>
        </>
    )
}
export default Followers
