import Sidebar from "../components/Sidebar"
import SelectAllOutlinedIcon from "@mui/icons-material/SelectAllOutlined"
import AddPost from "../components/AddPost"
import { useAppContext } from "../context/appContext"
import { useEffect } from "react"
import { useState } from "react"
import { Link } from "react-router-dom"

const Profile = () => {
    const {
        showAddPost,
        getUserPosts,
        findProfile,
        user,
        followUser,
        unfollowUser,
        refreshUserData,
    } = useAppContext()
    const [posts, setPosts] = useState()
    const [userData, setUserData] = useState(false)

    const [active, setActive] = useState()
    useEffect(() => {
        let name = window.location.pathname
        name = name.replace("/profile/", "")
        findProfile(name).then((res) => {
            setUserData(res)
            if (res.followings !== undefined) {
                setActive(res.followings.includes(user._id))
            }
            if (user._id === res._id) {
                refreshUserData(user.username)
            }
            console.log(res._id);
            getUserPosts(res._id).then((res) => {
                setPosts(res)
                console.log(res);
            })
        })
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const handleFollow = async (e) => {
        setActive(!active)
        e.preventDefault(e)
        console.log("follow")
        await followUser({
            _id: userData._id,
        })
    }

    const handleUnfollow = async (e) => {
        setActive(!active)
        e.preventDefault(e)
        console.log("unfollow")
        await unfollowUser({
            _id: userData._id,
        })
    }

    return (
        <>
            <Sidebar />
            {showAddPost === true && <AddPost />}
            {userData && posts && (
                <div className="profile">
                    <div className="profile__empty"></div>
                    <div className="profile__top">
                        <div className="profile__top-content">
                            <img
                                src={user?.img}
                                alt=""
                                className="profile__top-img"
                            />
                            <div className="profile__top-information">
                                <div className="profile__top-main">
                                    <div className="profile__top-username">
                                        {userData.username}
                                    </div>
                                    {user.username === userData.username ? (
                                        <Link to={`/editProfile`}>
                                            <button className="profile__top-edit">
                                                Edit profile
                                            </button>
                                        </Link>
                                    ) : active ? (
                                        <button
                                            className="profile__top-follow"
                                            onClick={handleFollow}
                                        >
                                            Follow
                                        </button>
                                    ) : (
                                        <button
                                            className="profile__top-followed"
                                            onClick={handleUnfollow}
                                        >
                                            Unfollow
                                        </button>
                                    )}
                                </div>

                                <div className="profile__top-statistics">
                                    <div className="profile__top-count">
                                        Posts:&nbsp;
                                        {posts === undefined ? "0" : posts.length}

                                    </div>
                                    <Link
                                        to={"followers"}
                                        style={{ textDecoration: "none" }}
                                        className="profile__top-count"
                                    >
                                        Followers:&nbsp;
                                        {userData.followers === undefined ? "0" : userData.followers.length}

                                    </Link>
                                    <Link
                                        to={"followings"}
                                        style={{ textDecoration: "none" }}
                                        className="profile__top-count"
                                    >
                                        Followings:&nbsp;
                                        {userData.followings === undefined ? "0" : userData.followings.length}
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="profile__biography">
                        <span>{user?.biography}</span>
                    </div>
                    <div className="profile__container">
                        <div className="profile__container-title">
                            <SelectAllOutlinedIcon className="profile__container-icon" />
                            Posts
                        </div>
                        <div className="profile__container-content">
                            {posts && posts.length !== 0 &&
                                posts.map((post) => {
                                    return (
                                        <Link
                                            key={post._id}
                                            to={
                                                process.env.PUBLIC_URL +
                                                "/post/" +
                                                post._id
                                            }
                                        >
                                            <img
                                                href=""
                                                key={post._id}
                                                src={post.img}
                                                alt=""
                                                className="profile__container-image"
                                            />
                                        </Link>
                                    )
                                })}
                        </div>
                    </div>
                    <div className="profile__footer"></div>
                </div>
            )}
        </>
    )
}
export default Profile
