import { useNavigate } from 'react-router-dom'
import PasswordOutlinedIcon from '@mui/icons-material/PasswordOutlined'
import { useAppContext } from '../context/appContext'
import { useEffect, useState } from 'react'
import Alert from '../components/Alert'

const initialState = {
    password: '',
    again: ''
}

let url = window.location.pathname
url = url.replace('/newPassword/', '')

const NewPassword = () => {
    const { user, showAlert, displayAlert, changePassword } = useAppContext()
    const navigate = useNavigate()

    const [values, setValues] = useState(initialState)

    const handleChange = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const { password, again } = values
        if (!password || !again) {
            displayAlert()
            return
        }
        const passwords = { password, repeatedPassword: again }
        changePassword({
            string: url,
            passwords,
            alertText: 'New password has been set'
        })
        setTimeout(() => {
            navigate('/login')
        }, 3000)
    }

    useEffect(() => {
        if (user) {
            setTimeout(() => {
                navigate('/')
            }, 1000)
        }
    }, [user, navigate])

    return (
        <div className="password">
            <div className="password__container">
                <div className="password__logo">
                    <PasswordOutlinedIcon className="password__logo-icon" />
                </div>
                <h3 className="password__title"> Password change </h3>
                <p className="password__paragraph">
                    Changing the password for the account:
                </p>
                <p className="password__email"> </p>
                <form className="password__form" onSubmit={onSubmit}>

                    {showAlert && <Alert />}
                    <label className="password__label"> </label>
                    <input
                        type="password"
                        className="password__input"
                        name="password"
                        placeholder="Password"
                        value={values.password}
                        onChange={handleChange}
                    />
                    <label className="password__label"> </label>
                    <input
                        type="password"
                        className="password__input"
                        name="again"
                        placeholder="Password again"
                        value={values.again}
                        onChange={handleChange}
                    />
                    <button type="submit" className="password__btn">
                        Save
                    </button>
                </form>
            </div>
        </div>
    )
}
export default NewPassword
