import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder'
import ChatBubbleOutlineOutlinedIcon from '@mui/icons-material/ChatBubbleOutlineOutlined'
import SendOutlinedIcon from '@mui/icons-material/SendOutlined'
import BookmarkBorderOutlinedIcon from '@mui/icons-material/BookmarkBorderOutlined'
import moment from 'moment'
import { useAppContext } from '../context/appContext'
import FavoriteOutlinedIcon from '@mui/icons-material/FavoriteOutlined'
import { useState } from 'react'
import Comment from './Comment.jsx'
import Alert from './Alert'
import { Link } from 'react-router-dom'

const Post = ({
    _id,
    author,
    description,
    img,
    authorImg,
    likes,
    comments,
    createdAt
}) => {
    const {
        likePost,
        user,
        cancelLikePost,
        displayAlert,
        addComment,
        showComments,
        showAlert
    } = useAppContext()
    const [commentDescription, setCommentDescription] = useState()
    const [commentsContainer, setCommentsContainer] = useState(null)
    const [showCommentsContainer, setShowCommentsContainer] = useState(false)

    const [active, setActive] = useState(likes.includes(user._id))

    const handleLikePost = async (e) => {
        setActive(!active)
        e.preventDefault(e)

        await likePost({
            _id
        })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        if (!commentDescription) {
            displayAlert()
            return
        }
        const comment = { description: commentDescription[''], id: _id }
        addComment({
            comment,
            alertText: 'Comment created!'
        })
    }

    const handleChangeDesc = (e) => {
        setCommentDescription({
            [e.target.name]: e.target.value
        })
    }

    const handleCancelLikePost = async (e) => {
        setActive(!active)
        e.preventDefault(e)

        await cancelLikePost({
            _id
        })
    }

    const displayComments = async () => {
        const comments = await showComments(_id)
        setCommentsContainer(comments)
    }

    const date2 = moment(createdAt).startOf('minutes').fromNow()
    return (
        <div className="postSmall">
            <div className="postSmall__top">
                <img
                    src={authorImg}
                    alt=""
                    className="postSmall__top-profile"
                />
                <Link
                    style={{ textDecoration: 'none' }}
                    to={`/profile/${author}`}
                    className="postSmall__top-name"
                >
                    {author}
                </Link>
            </div>
            <div className="postSmall__content">
                <img className="postSmall__content-img" src={img} alt="" />
            </div>
            <div className="postSmall__reaction">
                <div className="postSmall__reaction-list">

                    {active ? (
                        <FavoriteOutlinedIcon
                            className="postSmall__reaction-icon"
                            onClick={handleCancelLikePost}
                        />
                    ) : (
                        <FavoriteBorderIcon
                            className="postSmall__reaction-icon"
                            onClick={handleLikePost}
                        />
                    )}
                    <Link
                        to={`/post/${_id}`}
                        style={{ textDecoration: 'none', color: 'white' }}
                    >
                        <ChatBubbleOutlineOutlinedIcon className="postSmall__reaction-icon" />
                    </Link>
                    <SendOutlinedIcon className="postSmall__reaction-icon" />
                </div>
                <BookmarkBorderOutlinedIcon className="postSmall__reaction-icon" />
            </div>
            <div className="postSmall__bottom">
                <div className="postSmall__bottom-likes">
                    Likes: <strong> {likes.length - 1} </strong>
                </div>
                <div className="postSmall__bottom-description">
                    <strong> {author}: </strong> {description}
                </div>
            </div>
            <div className="postSmall__comments">

                {showCommentsContainer ? (
                    <div
                        className="postSmall__comments-button"
                        onClick={(e) => setShowCommentsContainer(false)}
                    >
                        seek comments
                    </div>
                ) : (
                    <div
                        className="postSmall__comments-button"
                        onClick={async (e) => {
                            commentsContainer === null &&
                                (await displayComments())
                            setShowCommentsContainer(true)
                        }}
                    >
                        show comments
                    </div>
                )}
                {showCommentsContainer === true &&
                    commentsContainer &&
                    commentsContainer.map((comment) => {
                        return <Comment key={comment._id} {...comment} />
                    })}
                <div className="postSmall__comments-time"> {date2} </div>
            </div>
            {showAlert && <Alert />}
            <div className="postSmall__comments-add">
                <input
                    type="text"
                    className="postSmall__comments-add-input"
                    placeholder="Add comment..."
                    onChange={handleChangeDesc}
                />
                <button
                    onClick={onSubmit}
                    className="postSmall__comments-add-btn"
                >
                    Upload
                </button>
            </div>
        </div>
    )
}
export default Post
