import Post from "../components/PostSmall"
import Sidebar from "../components/Sidebar"
import SmallProfile from "../components/SmallProfile"
import AddPost from "../components/AddPost"
import { useAppContext } from "../context/appContext"
import { useEffect } from "react"
import { Link } from "react-router-dom"

const Home = () => {
    const { getTimelineUser, posts, showAddPost, user } = useAppContext()

    useEffect(() => {
        const AllPosts = async () => {
            await getTimelineUser(user._id)
        }
        AllPosts()
        // eslint-disable-next-line
    }, [user])

    return (
        <>
            <Sidebar />
            {showAddPost === true && <AddPost />}
            <div className="home">
                <div className="posts">
                    {posts ? (
                        posts.map((post) => {
                            return <Post key={post._id} {...post} />
                        })
                    ) : (
                        <div className="posts__not">
                            <div className="posts__not-found">
                                UR NOT FOLLOWING ANYONE
                            </div>
                            <div className="posts__not-explore">
                                <span>
                                    You can find the users you are interested in
                                    in the section &nbsp;
                                </span>
                                <Link
                                    to="/explore"
                                    style={{
                                        color: "white",
                                    }}
                                >
                                    EXPLORE
                                </Link>
                            </div>
                        </div>
                    )}
                </div>
                <SmallProfile {...user} />
            </div>
        </>
    )
}
export default Home
