import { Link } from "react-router-dom"
import { useAppContext } from "../context/appContext"

const SmallProfile = ({ username }) => {
    const { user } = useAppContext()

    return (
        <div className="smallProfile">
            <img src={user.img} alt="" className="smallProfile__img" />
            <div className="smallProfile__data">
                <div className="smallProfile__data-username">{username}</div>
            </div>
            <div className="smallProfile__button">
                <Link
                    to={`/profile/${user.username}`}
                    style={{ textDecoration: "none" }}
                >
                    <div className="smallProfile__button-btn">Show Profile</div>
                </Link>
            </div>
        </div>
    )
}
export default SmallProfile
