import { BrowserRouter, Route, Routes } from 'react-router-dom'
import Home from './pages/Home'
import Login from './pages/Login'
import NewPassword from './pages/NewPassword'
import Profile from './pages/Profile'
import ProtectedRoute from './pages/ProtectedRoute'
import ResetPasswordRequest from './pages/ResetPasswordRequest'
import PopupPost from './pages/Post'
import EditProfile from './components/EditProfile'
import Followers from './components/Followers'
import Followings from './components/Followings'
import Explore from './pages/Explore'
import Favorite from './pages/Favorite'

function App() {
    return (
        <BrowserRouter>
            <Routes>
                <Route path="/login" element={<Login />} />
                <Route path="/password/reset" element={<ResetPasswordRequest />} />
                <Route path="/newPassword/:string" element={<NewPassword />} />
                <Route
                    path="/"
                    element={
                        <ProtectedRoute>
                            <Home />
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/explore"
                    element={
                        <ProtectedRoute>
                            <Explore />
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/editProfile"
                    element={
                        <ProtectedRoute>
                            <EditProfile />
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/profile/:username"
                    element={
                        <ProtectedRoute>
                            <Profile />
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/post/:id"
                    element={
                        <ProtectedRoute>
                            <PopupPost />
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/profile/:username/followers"
                    element={
                        <ProtectedRoute>
                            <Followers />
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/profile/:username/followings"
                    element={
                        <ProtectedRoute>
                            <Followings />
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/posts/favorite"
                    element={
                        <ProtectedRoute>
                            <Favorite />
                        </ProtectedRoute>
                    }
                />
            </Routes>
        </BrowserRouter>
    )
}

export default App
