import { useState } from "react"
import { useAppContext } from "../context/appContext"
import Sidebar from "./Sidebar"
import Alert from "./Alert"

const EditProfile = () => {
    const { user, displayAlert, updateUser, showAlert, sendEmail } =
        useAppContext()

    const initialState = {
        password: "",
        again: "",
    }

    const [values, setValues] = useState(initialState)
    const [username, setUsername] = useState(user?.username)
    const [name, setName] = useState(user?.name)
    const [biography, setBiography] = useState(user?.biography)
    const [file, setFile] = useState()
    const [selected, setSelected] = useState(1)

    const handleChange = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value })
    }

    const handleFileInputChange = (e) => {
        e.preventDefault()
        let files
        if (e.dataTransfer) {
            files = e.dataTransfer.files
        } else if (e.target) {
            files = e.target.files
        }

        const reader = new FileReader()
        reader.onload = () => {
            setFile(reader.result)
        }
        reader.readAsDataURL(files[0])
    }

    const onSubmit = (e) => {
        e.preventDefault()
        if (!username || !biography) {
            displayAlert()
            return
        }
        console.log(username);
        const updatedUser = { img: file, biography, username, name }
        updateUser({
            updatedUser,
            alertText: "User updated!",
        })
        setTimeout(() => {
            window.location.reload(false)
        }, 2000)
    }

    const onSubmitReset = (e) => {
        e.preventDefault()
        if (!user.email) {
            displayAlert()
            return
        }
        sendEmail({
            email: user.email,
            alertText: "Email Sended! Check your e-mail",
        })
    }

    return (
        <>
            <Sidebar />
            <div className="editProfile">
                <div className="editProfile__container">
                    <div className="editProfile__sidebar">
                        <div
                            className="editProfile__sidebar-point"
                            onClick={(e) => setSelected(1)}
                        >
                            Edit Profile
                        </div>
                        <div
                            className="editProfile__sidebar-point"
                            onClick={(e) => setSelected(2)}
                        >
                            Change password
                        </div>
                    </div>

                    {selected === 1 && (
                        <div className="editProfile__content">
                            <div className="editProfile__avatar">
                                {file ? (
                                    <img
                                        className="editProfile__avatar-picture"
                                        src={file}
                                        alt="chosen"
                                    />
                                ) : (
                                    <img
                                        className="editProfile__avatar-picture"
                                        src={user?.img}
                                        alt="profile"
                                    />
                                )}

                                <div className="editProfile__avatar-change">
                                    <div className="editProfile__avatar-name">
                                        {user?.email}
                                    </div>
                                    <label
                                        className="editProfile__avatar-btn"
                                        htmlFor="file"
                                    >
                                        Change your profile photo
                                        <input
                                            id="file"
                                            type="file"
                                            className="profile__form-input"
                                            name="image"
                                            hidden
                                            onChange={handleFileInputChange}
                                        />
                                    </label>
                                </div>
                            </div>
                            <div className="editProfile__body">
                                <div className="editProfile__body-term">
                                    first name and last name
                                </div>
                                <input
                                    className="editProfile__body-input"
                                    value={name || ""}
                                    onChange={(e) =>
                                        setName(e.target.value)
                                    }
                                // disabled
                                ></input>
                            </div>
                            <div className="editProfile__body-comment">
                                Help people discover your account by using a
                                name they know, be it your name, nickname or
                                company name.
                            </div>
                            <div className="editProfile__body">
                                <div className="editProfile__body-term">
                                    User name
                                </div>
                                <input
                                    className="editProfile__body-input"
                                    value={username || ""}
                                    onChange={(e) =>
                                        setUsername(e.target.value)
                                    }
                                ></input>
                            </div>
                            <div className="editProfile__body">
                                <div className="editProfile__body-term">
                                    biography
                                </div>
                                <textarea
                                    className="editProfile__body-textarea"
                                    rows="4"
                                    cols="50"
                                    value={biography || ""}
                                    onChange={(e) =>
                                        setBiography(e.target.value)
                                    }
                                ></textarea>
                            </div>
                            <div className="editProfile__body">
                                <div className="editProfile__body-term">
                                    email
                                </div>
                                <input
                                    className="editProfile__body-input"
                                    value={user.email}
                                    disabled
                                ></input>
                            </div>
                            {showAlert && <Alert />}
                            <div className="editProfile__body-button">
                                <button
                                    onClick={onSubmit}
                                    className="password__btn
                                "
                                >
                                    Upload
                                </button>
                            </div>
                        </div>
                    )}
                    {selected === 2 && (
                        <div className="editProfile__content">
                            <div className="editProfile__body">
                                <p className="password__paragraph">
                                    Press button and we'll send you a link to
                                    regain access to your account.
                                </p>
                                {showAlert && <Alert />}
                                <form
                                    className="password__form"
                                    onSubmit={onSubmitReset}
                                >
                                    <label className="password__label"></label>
                                    <input
                                        type="email"
                                        className="password__input"
                                        name="email"
                                        placeholder="Email"
                                        value={user?.email}
                                        onChange={handleChange}
                                    />
                                    <button
                                        type="submit"
                                        className="password__btn"
                                    >
                                        reset password
                                    </button>
                                </form>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </>
    )
}
export default EditProfile
