// import Logo from "./Logo"
import { Link, useNavigate } from 'react-router-dom'
import { useState, useEffect } from 'react'
import { useAppContext } from '../context/appContext.js'
import Alert from '../components/Alert'

const initialState = {
    username: '',
    email: '',
    password: '',
    isMember: true
}

const Login = () => {
    const { user, setupUser, showAlert, displayAlert } = useAppContext()

    const navigate = useNavigate()
    const [values, setValues] = useState(initialState)

    const toggleMember = () => {
        setValues({ ...values, isMember: !values.isMember })
    }

    const handleChange = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const { username, email, password, isMember } = values
        if (!email || !password || (!isMember && !username)) {
            displayAlert()
            return
        }
        const currentUser = { username, login: email, password }
        if (isMember) {
            setupUser({
                currentUser,
                endPoint: 'login',
                alertText: 'Login Successful! Redirecting...'
            })
            setTimeout(() => {
                navigate('/')
                window.location.reload(true)
            }, 3000)
        } else {
            setupUser({
                currentUser,
                endPoint: 'register',
                alertText: 'User Created! Refresh and log in'
            })
        }
    }

    useEffect(() => {
        if (user) {
            setTimeout(() => {
                navigate('/')
            }, 3000)
        }
        // eslint-disable-next-line
    }, [user, navigate])

    return (
        <div className="login">
            <div className="login__container">
                <div className="login__logo"> DogeStagram </div>
                <h3 className="login__title">

                    {values.isMember ? 'Login' : 'Register'}
                </h3>
                <form className="login__form" onSubmit={onSubmit}>

                    {showAlert && <Alert />}
                    {!values.isMember && (
                        <>
                            <label className="login__label"> </label>
                            <input
                                type="text"
                                className="login__input"
                                name="username"
                                placeholder="Name"
                                value={values.username}
                                onChange={handleChange}
                            />
                        </>
                    )}
                    <label className="login__label"> </label>
                    <input
                        type="email"
                        className="login__input"
                        name="email"
                        placeholder="Email"
                        value={values.email}
                        onChange={handleChange}
                    />
                    <label htmlFor="password" className="login__label">

                    </label>
                    <input
                        type="password"
                        name="password"
                        className="login__input"
                        placeholder="Password"
                        value={values.password}
                        onChange={handleChange}
                    />
                    <button type="submit" className="login__btn">
                        <p> {values.isMember ? 'Log in' : 'Sign up'} </p>
                    </button>
                </form>
                <div className="login__or">
                    <span> OR </span>
                </div>
                <div className="login__auth-google">
                    In future - auth with google
                </div>
                <Link to="/password/reset" style={{ textDecoration: 'none' }}>
                    <button className="login__btn-forgot-password">
                        Forgot password ?
                    </button>
                </Link>
            </div>
            <div className="login__container-register">
                <div className="login__register">
                    <p>

                        {values.isMember
                            ? 'Not a member yet?'
                            : 'Already a member?'}
                        <button
                            type="button"
                            onClick={toggleMember}
                            className="login__btn-member"
                        >
                            {values.isMember ? 'Register' : 'Login'}
                        </button>
                    </p>
                </div>
            </div>
        </div>
    )
}
export default Login
