import { useAppContext } from "../context/appContext"

const Alert = () => {
    const { alertType, alertText } = useAppContext()
    let text
    if (alertText === 851) {
        text = "Invalid credential"
    } else if (alertText === 858 || alertText === 859) {
        text = "Invalid credential"
    } else if (alertText === 860) {
        text = "Password are not the same"
    } else {
        text = alertText
    }

    return (
        <>{text && <div className={`alert alert-${alertType}`}>{text}</div>}</>
    )
}
export default Alert
