import LockOutlinedIcon from '@mui/icons-material/LockOutlined'
import { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { useAppContext } from '../context/appContext'
import Alert from '../components/Alert'

const ResetPasswordRequest = () => {
    const { user, sendEmail, showAlert, displayAlert } = useAppContext()
    const navigate = useNavigate()

    const [email, setEmail] = useState('')

    const handleChange = (e) => {
        setEmail(e.target.value)
    }

    const onSubmit = (e) => {
        e.preventDefault()
        console.log(email)
        if (!email) {
            displayAlert()
            return
        }
        sendEmail({
            email,
            alertText: 'Email Sended! Check your e-mail'
        })
    }

    useEffect(() => {
        if (user) {
            setTimeout(() => {
                navigate('/')
            }, 1000)
        }
    }, [user, navigate])

    return (
        <div className="password">
            <div className="password__container">
                <div className="password__logo">
                    <LockOutlinedIcon className="password__logo-icon" />
                </div>
                <h3 className="password__title"> Problems with log in ? </h3>
                <p className="password__paragraph">
                    Enter your email address and we 'll send you a link to regain access to your
                    account.
                </p>
                {showAlert && <Alert />}
                <form className="password__form" onSubmit={onSubmit}>
                    <label className="password__label"> </label>
                    <input
                        type="email"
                        className="password__input"
                        name="email"
                        placeholder="Email"
                        value={email}
                        onChange={handleChange}
                    />
                    <button type="submit" className="password__btn">
                        Send link to log in
                    </button>
                </form>
                <div className="password__or">
                    <span> OR </span>
                </div>
                <Link to="/login" style={{ textDecoration: 'none' }}>
                    <div className="password__link-register"> Create new account </div>
                </Link>
            </div>
        </div>
    )
}
export default ResetPasswordRequest
