import { useState } from "react"
import { useAppContext } from "../context/appContext"
import { useEffect } from "react"
import moment from "moment"
import Comment from "../components/Comment"
import Alert from "../components/Alert"
import Sidebar from "../components/Sidebar"
import { Link, useNavigate } from "react-router-dom"
import SettingsIcon from "@mui/icons-material/Settings"
import EditIcon from "@mui/icons-material/Edit"
import DeleteIcon from "@mui/icons-material/Delete"
import CheckIcon from "@mui/icons-material/Check"
import CloseIcon from "@mui/icons-material/Close"

const Post = () => {
    const {
        user,
        displayAlert,
        addComment,
        showComments,
        findPost,
        showAlert,
        deletePost,
        updatePost,
    } = useAppContext()
    const navigate = useNavigate()

    const [commentDescription, setCommentDescription] = useState()
    const [commentsContainer, setCommentsContainer] = useState()
    const [postData, setPostData] = useState()
    const [showSettings, setShowSettings] = useState(false)
    const [confirmDelete, setConfirmDelete] = useState(false)
    const [editPost, setEditPost] = useState(false)
    const [description, setDescription] = useState("")

    let date
    let _id = window.location.pathname
    _id = _id.replace("/post/", "")

    const displayComments = async () => {
        const comments = await showComments(_id)
        setCommentsContainer(comments)
    }

    const findPostData = async () => {
        console.log(_id);
        console.log(_id);
        const post = await findPost(_id)
        console.log(post);
        setPostData(post)
        setDescription(post?.description)
        date = moment(post.createdAt).startOf("minutes").fromNow()
    }

    const handleChangeDesc = (e) => {
        setCommentDescription({ [e.target.name]: e.target.value })
    }

    const handleChangeDescription = (e) => {
        setDescription(e.target.value)
    }

    const onSubmit = (e) => {
        e.preventDefault()
        if (!commentDescription) {
            displayAlert()
            return
        }
        const comment = { description: commentDescription[""], id: _id }
        addComment({
            comment,
            alertText: "Comment created!",
        })
    }

    const onSubmitPost = (e) => {
        e.preventDefault()
        if (!description) {
            displayAlert()
            return
        }
        const post = { _id, description }
        updatePost({
            post,
            alertText: "Post updated!",
        })
        setTimeout(() => {
            window.location.reload(false)
        }, 2000)
    }

    const handleDelete = (e) => {
        deletePost(postData._id)
        setTimeout(() => {
            navigate(`/profile/${postData.author}`)
        }, 1000)
    }

    useEffect(() => {
        findPostData()
        displayComments()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <>
            <div className="sidebar__darker">
                <Sidebar />
            </div>

            <div onClick={() => navigate(-1)}>
                <div className="post__background"></div>
            </div>
            <div className="post" onClick={(event) => event.preventDefault()}>
                <div className="post__container">
                    {postData && (
                        <>
                            <div className="post__img-container">
                                <img
                                    src={postData.img}
                                    alt=""
                                    className="post__img"
                                />
                            </div>
                            <div className="post__details">
                                <div className="post__top">
                                    <Link
                                        to={`/profile/${postData.author}`}
                                        style={{
                                            textDecoration: "none",
                                        }}
                                    >
                                        <img
                                            src={user.img}
                                            className="post__top-picture"
                                            alt="user"
                                        />
                                    </Link>
                                    <Link
                                        to={`/profile/${postData.author}`}
                                        style={{
                                            textDecoration: "none",
                                        }}
                                    >
                                        <div className="post__top-username">
                                            {postData.author}
                                        </div>
                                    </Link>
                                    <div className="post__settings">
                                        {showSettings && (
                                            <>
                                                <EditIcon
                                                    className="post__settings-icon"
                                                    onClick={(e) =>
                                                        setEditPost(!editPost)
                                                    }
                                                />
                                                <DeleteIcon
                                                    className="post__settings-icon"
                                                    onClick={(e) =>
                                                        setConfirmDelete(
                                                            !confirmDelete
                                                        )
                                                    }
                                                />
                                                {confirmDelete && (
                                                    <>
                                                        <CheckIcon
                                                            className="post__settings-icon"
                                                            onClick={(e) =>
                                                                handleDelete(
                                                                    postData._id
                                                                )
                                                            }
                                                        />
                                                        <CloseIcon
                                                            className="post__settings-icon"
                                                            onClick={(e) =>
                                                                setConfirmDelete(
                                                                    !confirmDelete
                                                                )
                                                            }
                                                        />
                                                    </>
                                                )}
                                            </>
                                        )}
                                        {postData.author === user.username && (
                                            <SettingsIcon
                                                className="post__settings-icon"
                                                onClick={(e) =>
                                                    setShowSettings(
                                                        !showSettings
                                                    )
                                                }
                                            />
                                        )}
                                    </div>
                                </div>
                                <div className="post__content">
                                    <div className="post__content-author">
                                        {postData.author}
                                    </div>
                                    <div className="post__content-description">
                                        {editPost ? (
                                            <div className="post__content-description-edit">
                                                <input
                                                    type="text"
                                                    value={description}
                                                    onChange={
                                                        handleChangeDescription
                                                    }
                                                    className="post__content-input"
                                                />
                                                <button
                                                    className="profile__top-follow"
                                                    onClick={onSubmitPost}
                                                >
                                                    Update
                                                </button>
                                            </div>
                                        ) : (
                                            <>{postData.description}</>
                                        )}
                                    </div>
                                </div>
                                {showAlert && <Alert />}
                                <div className="postSmall__comments">
                                    {commentsContainer &&
                                        commentsContainer.map((comment) => {
                                            return (
                                                <Comment
                                                    key={comment._id}
                                                    {...comment}
                                                />
                                            )
                                        })}
                                    <div className="postSmall__comments-time">
                                        {date}
                                    </div>
                                </div>
                                <div className="post__comments-add">
                                    <input
                                        type="text"
                                        className="post__comments-add-input"
                                        placeholder="Add comment..."
                                        onChange={handleChangeDesc}
                                    />
                                    <button
                                        onClick={onSubmit}
                                        className="postSmall__comments-add-btn"
                                    >
                                        Upload
                                    </button>
                                </div>
                            </div>
                        </>
                    )}
                </div>
            </div>
        </>
    )
}
export default Post
