import Sidebar from "../components/Sidebar"
import { useAppContext } from "../context/appContext"
import { useEffect } from "react"
import { useState } from "react"
import { Link } from "react-router-dom"
import FavoriteIcon from "@mui/icons-material/Favorite"

const Favorite = () => {
    const { likedPosts, user } = useAppContext()
    const [posts, setPosts] = useState()

    useEffect(() => {
        likedPosts(user._id).then((res) => {
            setPosts(res)
        })
        console.log(posts);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <>
            <Sidebar />
            {posts && (
                <div className="profile">
                    <div className="profile__name">{user.username}</div>
                    <div className="profile__container">
                        <div className="profile__container-title">
                            <FavoriteIcon className="profile__container-icon" />
                            &nbsp; Liked Posts
                        </div>
                        <div className="profile__container-content">
                            {posts && posts.length !== 0 &&
                                posts.map((post) => {
                                    return (
                                        <Link
                                            key={post._id}
                                            to={
                                                process.env.PUBLIC_URL +
                                                "/post/" +
                                                post._id
                                            }
                                        >
                                            <img
                                                href=""
                                                key={post._id}
                                                src={post.img}
                                                alt=""
                                                className="profile__container-image"
                                            />
                                        </Link>
                                    )
                                })}
                        </div>
                    </div>
                </div>
            )}
        </>
    )
}
export default Favorite
