import { useEffect, useRef, useState } from "react"
import Cropper from "react-cropper"
import "cropperjs/dist/cropper.css"
import { useAppContext } from "../context/appContext"
import Alert from "./Alert"

const AddPost = () => {
    const {
        displayAlert,
        addPost,
        showAlert,
        closePage,
        toggleAddPostPage,
        user,
    } = useAppContext()

    const [bigContainer, setBigContainer] = useState(false)

    const [image, setImage] = useState()
    const [cropData, setCropData] = useState()
    const [cropper, setCropper] = useState()
    const [description, setDescription] = useState()
    const imageRef = useRef(null)

    const onChange = (e) => {
        e.preventDefault()
        let files
        if (e.dataTransfer) {
            files = e.dataTransfer.files
        } else if (e.target) {
            files = e.target.files
        }

        const reader = new FileReader()
        reader.onload = () => {
            setImage(reader.result)
        }
        reader.readAsDataURL(files[0])
    }

    const getCropData = () => {
        if (typeof cropper !== "undefined") {
            setCropData(cropper.getCroppedCanvas().toDataURL())
        }
    }

    const handleChangeDesc = (e) => {
        setDescription({ [e.target.name]: e.target.value })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        console.log(image)
        if (!cropData || !description) {
            displayAlert()
            return
        }
        const post = { img: cropData, description: description.textarea }
        addPost({
            post,
            alertText: "Post created! Redirecting...",
            closePage: true,
        })
    }

    useEffect(() => {
        console.log(closePage)
        if (closePage) {
            setTimeout(() => {
                toggleAddPostPage()
                window.location.reload(false)
            }, 2000)
        }
        // eslint-disable-next-line
    }, [closePage])

    return (
        <>
            <div className="addPost">
                <button className="addPost__cancel" onClick={toggleAddPostPage}>
                    Cancel creating post
                </button>
                <div
                    className={
                        bigContainer === true
                            ? "addPost__container-big"
                            : "addPost__container"
                    }
                >
                    <div>
                        {cropData ? (
                            <>
                                <div className="addPost__title">
                                    <button
                                        className="addPost__btn-back"
                                        onClick={(e) => {
                                            setCropData(null)
                                            setBigContainer(false)
                                        }}
                                    >
                                        Back
                                    </button>
                                    Confirm your image selection
                                    {bigContainer === false && (
                                        <button
                                            className="addPost__btn-next"
                                            onClick={(e) =>
                                                setBigContainer(true)
                                            }
                                        >
                                            Next
                                        </button>
                                    )}
                                </div>
                                <img
                                    className="addPost__cropped"
                                    src={cropData}
                                    alt="cropped"
                                />
                            </>
                        ) : (
                            <div className="addPost__container">
                                <div className="addPost__title">
                                    Create new post
                                </div>
                                <form className="addPost__form">
                                    <label></label>
                                    <input
                                        type="file"
                                        className="addPost__input-file"
                                        onChange={onChange}
                                    />
                                    <Cropper
                                        className="addPost__copper"
                                        initialAspectRatio={1 / 1}
                                        aspectRatio={1}
                                        preview=".img-preview"
                                        src={image}
                                        ref={imageRef}
                                        viewMode={1}
                                        guides={true}
                                        background={false}
                                        responsive={true}
                                        checkOrientation={false}
                                        onInitialized={(instance) => {
                                            setCropper(instance)
                                        }}
                                    />
                                </form>
                                <button
                                    onClick={getCropData}
                                    className="addPost__btn"
                                >
                                    Crop Image
                                </button>
                            </div>
                        )}
                    </div>
                    {bigContainer === true && (
                        <>
                            <div className="addPost__container-1">
                                <div className="addPost__title">
                                    <button
                                        className="addPost__btn-back"
                                        onClick={onSubmit}
                                    >
                                        Upload
                                    </button>
                                    &nbsp;
                                </div>
                                <div className="addPost__user">
                                    <div className="addPost__user-username">
                                        {user.username}
                                    </div>
                                </div>
                                {showAlert && <Alert />}
                                <textarea
                                    onChange={handleChangeDesc}
                                    name="textarea"
                                    cols="30"
                                    rows="10"
                                    placeholder="Add description"
                                    className="addPost__textarea"
                                ></textarea>
                            </div>
                        </>
                    )}
                </div>
            </div>
        </>
    )
}
export default AddPost
