import HomeIcon from "@mui/icons-material/Home"
import SearchIcon from "@mui/icons-material/Search"
import ExploreIcon from "@mui/icons-material/Explore"
import SendIcon from "@mui/icons-material/Send"
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder"
import AddBoxIcon from "@mui/icons-material/AddBox"
import AccountCircleIcon from "@mui/icons-material/AccountCircle"
import { Link } from "react-router-dom"
import { useAppContext } from "../context/appContext"
import { useState } from "react"
import CheckIcon from "@mui/icons-material/Check"
import CloseIcon from "@mui/icons-material/Close"
import LogoutIcon from "@mui/icons-material/Logout"

const Sidebar = () => {
    const { toggleAddPostPage, user, logoutUser } = useAppContext()

    const [showLogout, setShowLogout] = useState(false)

    return (
        <>
            <div className="sidebar">
                <div className="sidebar__logo sidebar__menu-element">
                    <Link to="/" style={{ textDecoration: "none" }}>
                        <span>DOG-STAGRAM</span>
                    </Link>
                </div>
                <div className="sidebar__menu">
                    <div className="sidebar__menu-element">
                        <Link to="/" style={{ textDecoration: "none" }}>
                            <HomeIcon className="sidebar__menu-icon" />
                            <span> Home Page</span>
                        </Link>
                    </div>
                    <div className="sidebar__menu-element">
                        <SearchIcon className="sidebar__menu-icon" />
                        <span> Search</span>
                    </div>
                    <div className="sidebar__menu-element">
                        <Link to="/explore" style={{ textDecoration: "none" }}>
                            <ExploreIcon className="sidebar__menu-icon" />
                            <span> Explore</span>
                        </Link>
                    </div>
                    <div className="sidebar__menu-element">
                        <SendIcon className="sidebar__menu-icon" />
                        <span> Messages</span>
                    </div>
                    <div className="sidebar__menu-element">
                        <Link
                            to="/posts/favorite"
                            style={{ textDecoration: "none" }}
                        >
                            <FavoriteBorderIcon className="sidebar__menu-icon" />
                            <span> Favorite</span>
                        </Link>
                    </div>
                    <div className="sidebar__menu-element">
                        <AddBoxIcon
                            className="sidebar__menu-icon"
                            onClick={toggleAddPostPage}
                        />
                        <span onClick={toggleAddPostPage}> Add</span>
                    </div>

                    <div className="sidebar__menu-element">
                        <Link
                            to={`/profile/${user.username}`}
                            style={{ textDecoration: "none" }}
                        >
                            <AccountCircleIcon className="sidebar__menu-icon" />
                            <span>Profile</span>
                        </Link>
                    </div>
                    <div
                        className="sidebar__menu-element"
                        onClick={(e) => setShowLogout(!showLogout)}
                    >
                        <LogoutIcon className="sidebar__menu-icon" />
                        <span>Logout</span>
                    </div>
                    {showLogout && (
                        <>
                            <div
                                className="sidebar__menu-element"
                                onClick={(e) => logoutUser()}
                            >
                                <CheckIcon className="sidebar__menu-icon" />
                                <span>Yes, log me out</span>
                            </div>
                            <div
                                className="sidebar__menu-element"
                                onClick={(e) => setShowLogout(!showLogout)}
                            >
                                <CloseIcon className="sidebar__menu-icon" />
                                <span>No, don't log me out</span>
                            </div>
                        </>
                    )}
                </div>
            </div>
        </>
    )
}
export default Sidebar
