import React, { useContext, useReducer } from 'react'
import {
    TOGGLE_ADD_POST,
    SETUP_USER_BEGIN,
    SETUP_USER_SUCCESS,
    SETUP_USER_ERROR,
    DISPLAY_ALERT,
    CLEAR_ALERT,
    LOGOUT_USER,
    SEND_MAIL_BEGIN,
    SEND_MAIL_SUCCESS,
    SEND_MAIL_ERROR,
    CHANGE_PASSWORD_BEGIN,
    CHANGE_PASSWORD_SUCCESS,
    CHANGE_PASSWORD_ERROR,
    CREATE_POST_BEGIN,
    CREATE_POST_SUCCESS,
    CREATE_POST_ERROR,
    GET_ALL_POSTS_SUCCESS,
    GET_ALL_POSTS_BEGIN,
    CREATE_COMMENT_BEGIN,
    CREATE_COMMENT_SUCCESS,
    CREATE_COMMENT_ERROR,
    GET_ALL_POSTS_ERROR,
    UPDATE_USER_BEGIN,
    UPDATE_USER_SUCCESS,
    UPDATE_USER_ERROR,
    UPDATE_POST_BEGIN,
    UPDATE_POST_SUCCESS,
    UPDATE_POST_ERROR
} from './actions'

import reducer from './reducer'
import axios from 'axios'

const token = localStorage.getItem('token')
const user = localStorage.getItem('user')

const initialState = {
    isLoading: false,
    showAlert: false,
    alertText: '',
    alertType: '',
    user: user ? JSON.parse(user) : null,
    showAddPost: false,
    posts: [],
    token: token
}

const AppContext = React.createContext()

const AppProvider = ({ children }) => {
    const [state, dispatch] = useReducer(reducer, initialState)

    const authFetch = axios.create()

    authFetch.interceptors.request.use(
        (config) => {
            config.headers[`Authorization`] = `Bearer ${state.token}`
            return config
        },
        (error) => {
            return Promise.reject(error)
        }
    )

    authFetch.interceptors.response.use(
        (response) => {
            return response
        },
        (error) => {
            if (error.response.status === 401) {
                // logoutUser()
            }
            return Promise.reject(error)
        }
    )

    const addUserToLocalStorage = (user) => {
        localStorage.setItem('user', JSON.stringify(user))
    }

    const addTokenToLocalStorage = async (token) => {
        await localStorage.setItem('token', token)
    }

    const removeUserFromLocalStorage = () => {
        localStorage.removeItem('token')
        localStorage.removeItem('user')
    }

    const toggleAddPostPage = () => {
        dispatch({ type: TOGGLE_ADD_POST })
    }

    const setupUser = async ({ currentUser, endPoint, alertText }) => {
        dispatch({ type: SETUP_USER_BEGIN })
        try {
            const { data } = await axios.post(`/api/auth/${endPoint}`, currentUser)
            if (endPoint === 'login') {
                await addTokenToLocalStorage(data.token)
                addUserToLocalStorage(data.user)
            }
            dispatch({
                type: SETUP_USER_SUCCESS,
                payload: { user, token, alertText }
            })
        } catch (error) {
            console.log(error.response.data)
            dispatch({
                type: SETUP_USER_ERROR,
                payload: error.response.data.message
            })
        }
        clearAlert()
    }

    const refreshUserData = async (username) => {
        try {
            const data = await authFetch.get(`/api/profile/${username}`)
            addUserToLocalStorage(data.data._doc)
        } catch (error) {
            console.log(error)
        }
    }

    const displayAlert = () => {
        dispatch({ type: DISPLAY_ALERT })
        clearAlert()
    }

    const clearAlert = () => {
        setTimeout(() => {
            dispatch({ type: CLEAR_ALERT })
        }, 3000)
    }

    const logoutUser = () => {
        dispatch({ type: LOGOUT_USER })
        removeUserFromLocalStorage()
    }

    const sendEmail = async ({ email, alertText }) => {
        dispatch({ type: SEND_MAIL_BEGIN })
        try {
            await axios.post(`/api/auth/forgotPassword`, { email: email })
            dispatch({
                type: SEND_MAIL_SUCCESS,
                payload: alertText
            })
        } catch (error) {
            dispatch({
                type: SEND_MAIL_ERROR,
                payload: { msg: error.response.data }
            })
        }
        clearAlert()
    }

    const changePassword = async ({ passwords, alertText, string }) => {
        dispatch({ type: CHANGE_PASSWORD_BEGIN })
        try {
            await axios.patch(`/api/auth/newPassword/${string}`, passwords)
            dispatch({
                type: CHANGE_PASSWORD_SUCCESS,
                payload: alertText
            })
        } catch (error) {
            console.log(error)
            dispatch({
                type: CHANGE_PASSWORD_ERROR,
                payload: { msg: error.response.data.message }
            })
        }
        clearAlert()
    }

    const addPost = async ({ post, alertText, closePage }) => {
        dispatch({ type: CREATE_POST_BEGIN })

        let currentUser = JSON.parse(user)
        post['author'] = currentUser._id
        try {
            await authFetch.post(`/api/post`, post)
            dispatch({
                type: CREATE_POST_SUCCESS,
                payload: { alertText, closePage }
            })
        } catch (error) {
            dispatch({
                type: CREATE_POST_ERROR,
                payload: { msg: error.response.data }
            })
        }
    }

    const getTimelineUser = async () => {
        dispatch({ type: GET_ALL_POSTS_BEGIN })

        let currentUser = JSON.parse(user)

        try {
            const { data } = await authFetch.get(`/api/posts/timeline/${currentUser._id}`)
            if (!data) {
                console.log('not found')
            }
            dispatch({
                type: GET_ALL_POSTS_SUCCESS,
                payload: {
                    posts: data
                }
            })
        } catch (error) {
            dispatch({
                type: GET_ALL_POSTS_ERROR,
                payload: { msg: error.response.data }
            })
        }
    }

    const getExploreUser = async () => {
        dispatch({ type: GET_ALL_POSTS_BEGIN })
        let currentUser = JSON.parse(user)

        try {
            const { data } = await authFetch.get(`/api/posts/explore/${currentUser._id}`)
            if (!data) {
                console.log('not found')
            }
            dispatch({
                type: GET_ALL_POSTS_SUCCESS,
                payload: {
                    posts: data
                }
            })
        } catch (error) {
            dispatch({
                type: GET_ALL_POSTS_ERROR,
                payload: { msg: error.response.data }
            })
        }
    }

    const likePost = async ({ _id }) => {
        let currentUser = JSON.parse(user)
        try {
            await authFetch.post(`/api/post/like/${_id}`, {
                userId: currentUser._id
            })
        } catch (error) {
            console.log(error)
        }
    }

    const cancelLikePost = async ({ _id }) => {
        let currentUser = JSON.parse(user)
        try {
            await authFetch.patch(`/api/post/unlike/${_id}`, {
                userId: currentUser._id
            })
        } catch (error) {
            console.log(error)
        }
    }

    const addComment = async ({ comment, alertText }) => {
        dispatch({ type: CREATE_COMMENT_BEGIN })
        let currentUser = JSON.parse(user)
        try {
            await authFetch.post(`/api/comment/add/${comment.id}`, {
                description: comment.description,
                username: currentUser.username
            })
            dispatch({
                type: CREATE_COMMENT_SUCCESS,
                payload: alertText
            })
        } catch (error) {
            dispatch({
                type: CREATE_COMMENT_ERROR,
                payload: { msg: error.response.data }
            })
        }
        clearAlert()
    }

    const showComments = async (postId) => {
        try {
            const comments = await authFetch.get(`/api/comment/get/${postId}`)
            return comments.data[0]
        } catch (error) {
            console.log(error)
        }
    }

    const getUserPosts = async (id) => {
        try {
            const posts = await authFetch.get(`/api/posts/${id}`)
            return posts.data
        } catch (error) {
            console.log(error)
        }
    }

    const findPost = async (_id) => {
        try {
            const post = await authFetch.get(`/api/post/${_id}`)
            return post.data
        } catch (error) {
            console.log(error)
        }
    }

    const findProfile = async (username) => {
        try {
            const user = await authFetch.get(`/api/profile/${username}`)
            console.log(user)
            return user.data._doc
        } catch (error) {
            console.log(error)
        }
    }

    const followUser = async ({ _id }) => {
        let currentUser = JSON.parse(user)

        try {
            await authFetch.post(`/api/user/follow/${currentUser._id}`, {
                userId: _id
            })
        } catch (error) {
            console.log(error)
        }
    }

    const unfollowUser = async ({ _id }) => {
        let currentUser = JSON.parse(user)
        try {
            await authFetch.patch(`/api/user/unfollow/${currentUser._id}`, {
                userId: _id
            })
        } catch (error) {
            console.log(error)
        }
    }

    const updateUser = async ({ updatedUser, alertText }) => {
        let currentUser = JSON.parse(user)

        dispatch({ type: UPDATE_USER_BEGIN })
        try {
            await authFetch.patch(`/api/user/update/${currentUser._id}`, updatedUser)
            dispatch({
                type: UPDATE_USER_SUCCESS,
                payload: alertText
            })
        } catch (error) {
            console.log(error)
            dispatch({
                type: UPDATE_USER_ERROR,
                payload: { msg: error.response.data }
            })
        }
    }

    const getUserFollowers = async (_id) => {
        try {
            const followers = await authFetch.get(`/api/user/${_id}/followers`)
            return followers.data
        } catch (error) {
            console.log(error)
        }
    }

    const getUserFollowings = async (_id) => {
        try {
            const followings = await authFetch.get(`/api/user/${_id}/followings`)
            return followings.data
        } catch (error) {
            console.log(error)
        }
    }

    const deletePost = async (_id) => {
        try {
            await authFetch.delete(`/api/post/${_id}`)
            console.log('delete')
        } catch (error) {
            console.log(error)
        }
    }

    const updatePost = async ({ post, alertText }) => {
        dispatch({ type: UPDATE_POST_BEGIN })

        try {
            await authFetch.patch(`/api/post/${post._id}`, {
                description: post.description
            })
            dispatch({
                type: UPDATE_POST_SUCCESS,
                payload: alertText
            })
        } catch (error) {
            dispatch({
                type: UPDATE_POST_ERROR,
                payload: { msg: error.response.data }
            })
        }
    }

    const likedPosts = async (_id) => {
        try {
            const posts = await authFetch.get(`/api/posts/${_id}/liked`)
            return posts.data
        } catch (error) {
            console.log(error)
        }
    }

    return (
        <AppContext.Provider
            value={{
                ...state,
                toggleAddPostPage,
                logoutUser,
                setupUser,
                displayAlert,
                sendEmail,
                changePassword,
                addPost,
                getTimelineUser,
                likePost,
                cancelLikePost,
                addComment,
                showComments,
                getUserPosts,
                findPost,
                findProfile,
                followUser,
                unfollowUser,
                updateUser,
                refreshUserData,
                getUserFollowers,
                getUserFollowings,
                getExploreUser,
                deletePost,
                updatePost,
                likedPosts
            }}>
            {children}
        </AppContext.Provider>
    )
}

const useAppContext = () => {
    return useContext(AppContext)
}

export { AppProvider, initialState, useAppContext }
