import {
    SETUP_USER_BEGIN,
    SETUP_USER_SUCCESS,
    SETUP_USER_ERROR,
    DISPLAY_ALERT,
    CLEAR_ALERT,
    LOGOUT_USER,
    SEND_MAIL_BEGIN,
    SEND_MAIL_SUCCESS,
    SEND_MAIL_ERROR,
    CHANGE_PASSWORD_BEGIN,
    CHANGE_PASSWORD_SUCCESS,
    CHANGE_PASSWORD_ERROR,
    CREATE_POST_BEGIN,
    CREATE_POST_SUCCESS,
    CREATE_POST_ERROR,
    TOGGLE_ADD_POST,
    GET_ALL_POSTS_BEGIN,
    GET_ALL_POSTS_SUCCESS,
    GET_ALL_POSTS_ERROR,
    CREATE_COMMENT_BEGIN,
    CREATE_COMMENT_SUCCESS,
    CREATE_COMMENT_ERROR,
    UPDATE_USER_BEGIN,
    UPDATE_USER_SUCCESS,
    UPDATE_USER_ERROR,
    UPDATE_POST_BEGIN,
    UPDATE_POST_SUCCESS,
    UPDATE_POST_ERROR
} from './actions'

const reducer = (state, action) => {
    if (action.type === TOGGLE_ADD_POST) {
        return {
            ...state,
            showAddPost: !state.showAddPost
        }
    }
    if (action.type === LOGOUT_USER) {
        return {
            ...state,
            user: null,
            token: null,
            followings: null
        }
    }
    if (action.type === DISPLAY_ALERT) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: 'Please provide all values!'
        }
    }
    if (action.type === CLEAR_ALERT) {
        return {
            ...state,
            showAlert: false,
            alertType: '',
            alertText: ''
        }
    }
    if (action.type === SETUP_USER_BEGIN) {
        return {
            ...state
        }
    }
    if (action.type === SETUP_USER_SUCCESS) {
        return {
            ...state,
            token: action.payload.token,
            user: action.payload.user,
            showAlert: true,
            alertType: 'success',
            alertText: action.payload.alertText
        }
    }
    if (action.type === SETUP_USER_ERROR) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: action.payload
        }
    }
    if (action.type === SEND_MAIL_BEGIN) {
        return {
            ...state
        }
    }
    if (action.type === SEND_MAIL_SUCCESS) {
        console.log(action)
        return {
            ...state,
            showAlert: true,
            alertType: 'success',
            alertText: action.payload
        }
    }
    if (action.type === SEND_MAIL_ERROR) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: action.payload
        }
    }
    if (action.type === CHANGE_PASSWORD_BEGIN) {
        return {
            ...state
        }
    }
    if (action.type === CHANGE_PASSWORD_SUCCESS) {
        return {
            ...state,
            token: action.payload.token,
            user: action.payload.user,
            showAlert: true,
            alertType: 'success',
            alertText: action.payload
        }
    }
    if (action.type === CHANGE_PASSWORD_ERROR) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: action.payload.msg
        }
    }
    if (action.type === CREATE_POST_BEGIN) {
        return {
            ...state,
            isLoading: true
        }
    }
    if (action.type === CREATE_POST_SUCCESS) {
        return {
            ...state,
            alertText: action.payload.alertText,
            showAlert: true,
            alertType: 'success',
            closePage: action.payload.closePage
        }
    }
    if (action.type === CREATE_POST_ERROR) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: action.payload.msg
        }
    }
    if (action.type === GET_ALL_POSTS_BEGIN) {
        return {
            ...state,
            showAlert: false
        }
    }
    if (action.type === GET_ALL_POSTS_SUCCESS) {
        return {
            ...state,
            posts: action.payload.posts
        }
    }
    if (action.type === GET_ALL_POSTS_ERROR) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: action.payload.msg
        }
    }
    if (action.type === CREATE_COMMENT_BEGIN) {
        return {
            ...state,
            isLoading: true
        }
    }
    if (action.type === CREATE_COMMENT_SUCCESS) {
        return {
            ...state,
            alertText: action.payload,
            showAlert: true,
            alertType: 'success'
        }
    }
    if (action.type === CREATE_COMMENT_ERROR) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: action.payload.msg
        }
    }
    if (action.type === UPDATE_USER_BEGIN) {
        return {
            ...state,
            isLoading: true
        }
    }
    if (action.type === UPDATE_USER_SUCCESS) {
        return {
            ...state,
            alertText: action.payload,
            showAlert: true,
            alertType: 'success',
            closePage: action.payload.closePage
        }
    }
    if (action.type === UPDATE_USER_ERROR) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: action.payload.msg
        }
    }
    if (action.type === UPDATE_POST_BEGIN) {
        return {
            ...state,
            isLoading: true
        }
    }
    if (action.type === UPDATE_POST_SUCCESS) {
        return {
            ...state,
            alertText: action.payload,
            showAlert: true,
            alertType: 'success'
        }
    }
    if (action.type === UPDATE_POST_ERROR) {
        return {
            ...state,
            showAlert: true,
            alertType: 'danger',
            alertText: action.payload.msg
        }
    }
    throw new Error(`no such action: ${action.type}`)
}

export default reducer
